<template>
    <div class="bar">
        <div class="bar__text">
            <div class="text__title">{{taskContent.title}}</div>
            <div class="text__description">{{taskContent.desc}}</div>
        </div>
        <button class="bar__done-button" @click="callDeleteEvent()">Done</button>
    </div>
</template>

<script>

export default {
    name: 'toDoBar',
    props: ['taskContent', 'taskNumber'],
    emits: ['deleteTask'],
    methods: {
        callDeleteEvent() {
            this.$emit('deleteTask', this.taskNumber);
        }
    }
}
</script>

<style scoped>
.bar {
    color: grey;
    max-height: 7rem;
    font-size: 1.2rem;
    margin: 10px;
    display: flex;
}

.bar__text {
    display: inline-flex;
    flex-direction: column;
    width: 75%;
}

.text__description {
    padding-right: 7px;
    overflow: auto;
}

.text__title {
    color: #05386B;
}

.bar__done-button {
    border: 0;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 1rem;
    color: #379683;
    padding: 3px 5px;
    background-color: white;
    transition: all 0.2s ease-in-out;
    border-radius: 3px;
    width: 25%;
}

.bar__done-button:hover {
    background-color: #8EE4AF;
}

</style>