<template>
    <div class="to-do-list" ref="toDoListEntity">
        <div class="to-do-list__header" ref="toDoListHeaderEntity">
            <div class="header__text">Tasks</div>
            <div class="header__task-move"><i class="fa-solid fa-arrows-up-down-left-right"></i></div>
        </div>
        <div class="to-do-list__bars">
            <to-do-bar-component v-for="(task, taskNum) in tasksList" :key="taskNum" :taskContent="task" :taskNumber="taskNum"
            @deleteTask="delTask"/>
        </div>
        <div class="to-do-list__add-task">
            <input type="text" 
            placeholder="Task title" 
            maxlength="30"
            class="add-task__title inputs" 
            :class="{emptyInputStatus: emptyTitle}" 
            v-model="newTaskTitle">
            
            <textarea type="text" 
            placeholder="Task description" 
            class="add-task__description inputs" 
            :class="{emptyInputStatus: emptyDesc}" 
            v-model="newTaskDesc"></textarea>
            
            <button class="add-task__button" @click="addNewTask()">âž•</button>
        </div>
    </div>
</template>

<script>
import toDoBarComponent from '@/components/todoBarComponent.vue';

export default {
    name: 'toDoList',
    components: {
        toDoBarComponent
    },
    data() {
        return {
            newTaskTitle: '',
            newTaskDesc: '',
            emptyTitle: false,
            emptyDesc: false,
            moveStatus: false,
            tasksList: [
                {title: "Task 1", desc: "Description 1"},
                {title: "Task 2", desc: "Description 2"},
                {title: "Task 3", desc: "Description 3"},
                {title: "Task 4", desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut laborum perferendis veritatis error quidem at nam non deserunt, qui reiciendis?"},
                {title: "Task 5", desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut laborum perferendis veritatis error quidem at nam non deserunt, qui reiciendis?"},
            ]
        }
    },
    mounted() {
        function moveToDoList({movementX, movementY}) {
            
            if(this.moveStatus) {
                
                let toDoListEnt = this.$refs.toDoListEntity;
                let getStyle = window.getComputedStyle(toDoListEnt);
                let leftToDoListVal = parseInt(getStyle.left);
                let topToDoListVal = parseInt(getStyle.top);
                
                if(leftToDoListVal + movementX > 0 && leftToDoListVal + movementX + parseInt(getStyle.width) < window.innerWidth) {
                    toDoListEnt.style.left = `${leftToDoListVal + movementX}px`;
                }
                if(topToDoListVal + movementY > 0 && topToDoListVal + movementY + parseInt(getStyle.height) < window.innerHeight) {
                    toDoListEnt.style.top = `${topToDoListVal + movementY}px`;
                }
            }
        }

        this.$refs.toDoListHeaderEntity.addEventListener("mousedown", () => this.moveStatus = true);
        this.$refs.toDoListHeaderEntity.addEventListener("mousemove", moveToDoList.bind(this));
        this.$refs.toDoListHeaderEntity.addEventListener("mouseup", () => this.moveStatus = false);
        this.$refs.toDoListHeaderEntity.addEventListener("mouseout", () => this.moveStatus = false);
    },
    methods: {
        addNewTask() {
            if(this.newTaskTitle && this.newTaskDesc) {
                this.tasksList.push({title: this.newTaskTitle, desc: this.newTaskDesc});
                this.newTaskTitle = '';
                this.newTaskDesc = '';
                this.emptyTitle = false;
                this.emptyDesc = false;
            }
            else {
                if(!this.newTaskTitle) {
                    this.emptyTitle = true;
                }
                if(!this.newTaskDesc) {
                    this.emptyDesc = true;
                }
            }
        },
        delTask(numTask) {
            this.tasksList.splice(numTask, 1);
        }
    }
}
</script>

<style scoped>
.to-do-list {
    background-color: white;
    position: absolute;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 1.5rem;
    color: white;
    width: 300px;
    height: 450px;
    box-shadow: 0 0 5px 5px grey;

    display: flex;
    flex-direction: column;
}

.to-do-list__header {
    background-color: #05386B;
    padding: 5px 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.to-do-list__header > * {
    padding: 5px;
}

.header__task-move {
    font-size: 24px;
    cursor: pointer;
}

.to-do-list__bars {
    height: 75%;
}

.to-do-list__add-task {
    display: grid;
    grid-template-areas: "inpTitle btn"
                         "inpDesc btn";

    justify-content: space-around;
    padding: 10px;
    border-top: 1px solid grey;
    height: 150px;
}

.inputs {
    margin: 2px;
    width: 200px;
    font-family: 'Josefin Sans', sans-serif;
}

.add-task__title {
    grid-area: inpTitle;
    height: 1.5rem;
    font-size: 1rem;
}

.add-task__description {
    grid-area: inpDesc;
    height: 3rem;
    font-size: 1rem;
    resize: none;
}

.add-task__button {
    grid-area: btn;
    height: 50px;
    width: 50px;
    align-self: center;
    border: 0;
    background-color: white;
    transition: background-color 0.2s ease-in-out;
}

.add-task__button:hover {
    border: 1px solid rgb(201, 201, 201);
    background-color: rgb(219, 219, 219);
    border-radius: 3px;
}

.to-do-list__bars {
    overflow: auto;
}

.emptyInputStatus {
    box-shadow: 0 0 3px 3px #ffc6ae;
}

.inputs {
    border: 1px solid rgb(217, 217, 217);
}

</style>